@media screen and (min-width: 600px) {
	.site-footer {
		padding: 1em 2em;
	}
}

.social-navigation ul {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.social-navigation ul a {
	display: block;
